#app4{

}
@keyframes  change {
    0%{
    background: yellow;
    }
    100%{
    background: coral;
    }
}
#app4 .circle{
    width:30vw;
    height: 30vw;
    border-radius: 50%;
    border: 1px solid darkseagreen;
}
#app4 .circle.active{
    animation: change 1s infinite alternate linear;
}